.about {
	// .about__container

	&__container {
		display: grid;
		gap: toRem(90);
		align-items: center;
		@media (min-width: $mobile) {
			grid-template-columns: 44.219% 1fr;
		}
		@media (max-width: $mobile) {
			@include adaptiveValue("gap", 70, 50, 0, 767.98, 320);
		}
	}

	&__image-wrapp {
		position: relative;

		&::before,
		&::after {
			content: "";
			position: absolute;
			width: percent(137, 543);
			height: percent(131, 431);
			background: url("@img/decors/decor-dots.png") 0 0 / contain no-repeat;
			z-index: -1;
		}
		&::before {
			right: -10%;
			top: -12%;
			@media (max-width: $mobile) {
				top: -8%;
			}
		}
		&::after {
			bottom: -12%;
			left: -14%;
			@media (max-width: $mobile) {
				bottom: -8%;
			}
		}
		@media (max-width: $mobile) {
			order: 2;
		}
	}

	// .about__image

	&__image {
		padding-bottom: percent(431, 543);
		border-radius: 0 toRem(24) toRem(78) toRem(24);
		overflow: hidden;
		@media (max-width: $mobile) {
			border-radius: 0 toRem(12.5) toRem(40.6) toRem(12.5);
			img {
				border-radius: 0 0.78125rem 6.5375rem 0.78125rem;
			}
		}
	}

	&__image-bg {
		position: absolute;
		bottom: -7%;
		right: -7%;
		width: percent(453, 543);
		height: percent(324, 431);
		background: url("@img/decors/decor-bg.png") 0 0 / contain no-repeat;
		z-index: -1;
		@media (max-width: $mobile) {
			bottom: -3%;
			right: -3%;
		}
	}

	// .about__content

	&__content {
		&.content {
			.content__title {
				@include adaptiveValue("font-size", 44, 30, 0, $containerWidth, 767.98);
				&:not(:last-child) {
					margin-bottom: toRem(30);
				}
				&.title-decor {
					&::after {
						width: 41%;
					}
				}
			}

			.content__text {
				@media (min-width: $mobile) {
					max-width: toRem(530);
				}
			}
		}
		@media (max-width: $mobile) {
			order: 1;
		}
	}
}
