.footer {
	padding-top: toRem(239);
	padding-bottom: toRem(117);
	background-color: var(--primary-color);
	color: var(--white-color);
	position: relative;
	@media (max-width: $mobile) {
		padding-bottom: toRem(50);
		padding-top: toRem(50);
	}
	&::before {
		content: "";
		position: absolute;
		top: toRem(-3);
		left: 0;
		width: 100%;
		height: 100%;
		background: url("@img/decors/bg-lines-white.png") 0 0 / 100% auto no-repeat;
		pointer-events: none;
		@media (max-width: $mobile) {
			transform: rotate(180deg);
			background-image: url("@img/decors/bg-lines-dark.png");
			height: 37px;
			top: -36px;
		}
	}
	// .footer__container

	&__container {
		display: grid;
		gap: toRem(28);
		@media (max-width: $mobile) {
			gap: toRem(50);
		}
	}

	// .footer__content

	&__header {
		text-align: center;

		&.content {
			.content__title {
				font-weight: 700;
				display: inline-block;
				max-width: toRem(1100);
				margin: 0 auto;
				&:not(:last-child) {
					margin-bottom: toRem(20);
					@media (max-width: $mobile) {
						margin-bottom: toRem(15);
					}
				}
				&.title-decor {
					&::after {
						margin-top: toRem(30);
						@media (max-width: $mobile) {
							margin-top: toRem(15);
							background-color: var(--white-color);
						}
					}
				}
			}

			.content__text {
				font-size: toRem(22);
				max-width: toRem(1024);
				margin: 0 auto;
				@media (max-width: $mobileSmall) {
					font-size: toRem(14);
				}
			}
		}
	}
}
