.programs {
	padding-top: toRem(120);
	padding-bottom: toRem(60);
	position: relative;
	@media (max-width: $mobile) {
		padding-top: toRem(70);
		padding-bottom: toRem(40);
	}
	&::before {
		content: "";
		position: absolute;
		top: toRem(-3);
		left: 0;
		width: 100%;
		height: 100%;
		pointer-events: none;
		background: url("@img/decors/bg-lines-dark.png") 0 0 / 100% auto no-repeat;
	}
	// .programs__container

	&__container {
	}

	// .programs__header

	&__header {
		@media (min-width: $mobile) {
			max-width: toRem(1060);
			margin: 0 auto;
		}
		text-align: center;
		@media (max-width: $mobile) {
			text-align: left;
		}
		&:not(:last-child) {
			margin-bottom: toRem(30);
		}
		&.content {
			.content__title {
			}
		}
	}

	// .programs__list

	&__list {
		display: grid;

		// gap: toRem(50);
		max-width: toRem(1024);
		margin: 0 auto;
		position: relative;
		@media (min-width: $mobile) {
			grid-template-columns: repeat(2, 1fr);
			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 50%;
				transform: translateX(-50%);
				width: toRem(1);
				height: 100%;
				background-color: rgba(0, 0, 0, 0.2);
			}
		}
		@media (max-width: $mobile) {
			gap: toRem(20);
		}
	}
}

.item-program {
	--padding: 43px;
	@media (max-width: $tablet) {
		--padding: 30px;
	}
	@media (min-width: $mobile) {
		&:nth-child(odd) {
			padding-right: var(--padding);
		}
		&:nth-child(even) {
			padding-left: var(--padding);
		}
	}
	@media (max-width: $mobile) {
		&:not(:last-child) {
			&::after {
				content: "";
				display: block;
				width: 100%;
				height: toRem(1);
				background-color: rgba(0, 0, 0, 0.2);
				margin-top: toRem(30);
			}
		}
	}

	// .item-program__content

	&__content {
		text-align: center;
		&:not(:last-child) {
			margin-bottom: toRem(25);
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(20);
			}
		}
		&.content {
			.content__title {
				display: inline-block;
				font-size: toRem(24);
				&:not(:last-child) {
					margin-bottom: toRem(10);
				}
				&.title-decor {
					&::after {
						height: toRem(2);
						@media (max-width: $mobileSmall) {
							margin-top: toRem(5);
						}
					}
				}
				@media (max-width: $mobileSmall) {
					font-size: toRem(22);
				}
			}

			.content__text {
				@media (max-width: $mobileSmall) {
					font-size: toRem(14);
				}
			}
		}
	}

	// .item-program__image

	&__image {
		padding-bottom: percent(302, 457);
		border-radius: toRem(24);
		overflow: hidden;
		@media (max-width: $mobile) {
			border-radius: toRem(15);
		}
	}
}
