.faq {
	padding-top: toRem(165);
	padding-bottom: toRem(90);
	@media (max-width: $tablet) {
		padding-top: toRem(50);
	}
	@media (max-width: $mobile) {
		padding-bottom: toRem(50);
	}

	// .faq__container

	&__container {
		display: grid;
		align-items: start;

		@include adaptiveValue("gap", 194, 100, 0, $containerWidth, 991.98);
		@media (min-width: $tablet) {
			grid-template-columns: 1fr 30.731861%;
		}
	}

	&__body {
		display: grid;
		gap: toRem(40);
		@media (max-width: $mobile) {
			gap: toRem(18);
		}
	}

	// .faq__content

	&__content {
		&.content {
			.content__title {
				font-weight: 700;
			}
			.content__text {
			}
		}
	}

	&__spollers {
	}
	&__image-wrapp {
		position: relative;
		align-self: end;
		@media (min-width: $mobile) {
			margin-top: toRem(-50);
		}
		@media (max-width: $tablet) {
			display: none;
		}
		&::before,
		&::after {
			content: "";
			position: absolute;
			background: url("@img/decors/decor-dots.png") 0 0 no-repeat;
			width: 26.913858%;
			height: 18.790451%;
			z-index: -1;
		}
		&::before {
			top: 42%;
			right: toRem(-60);
		}
		&::after {
			bottom: -6%;
			left: -14%;
		}
	}

	&__image-bg {
		position: absolute;
		bottom: 0;
		width: 100%;
		height: percent(273, 534);
		z-index: 1;
		background: url("@img/decors/decor-bg.png") 0 0 / contain no-repeat;
	}

	&__image {
		padding-bottom: percent(534, 377);
		position: relative;
		z-index: 2;
	}
}

// .faq__spollers

.spollers {
	display: grid;
}
// 
.spollers__item {
	&:first-child {
		border-top: toRem(3) solid var(--white-light-color);
	}
	border-bottom: toRem(3) solid var(--white-light-color);
}
//  
.spollers__title {
	font-family: var(--secondary-font);
	font-size: toRem(18);
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: toRem(18);
	width: 100%;
	cursor: pointer;
	text-align: left;
	padding: toRem(26) toRem(10) toRem(26) 0;
	list-style: none;
	font-weight: 600;
	line-height: 1.3;
	transition: color 0.3s;
	@media (max-width: $mobileSmall) {
		font-size: toRem(14);
		padding: toRem(23) toRem(10) toRem(23) 0;
	}
	@media (any-hover: hover) {
		&:hover {
			color: var(--hover--color);
		}
	}
	&::marker,
	&::-webkit-details-marker {
		display: none;
	}

	&._spoller-active {
		color: var(--primary-color);
		.spollers__icon {
			transform: rotate(45deg);
		}
	}
}
.spollers__icon {
	flex: 0 0 toRem(12);
	width: toRem(12);
	height: toRem(12);
	transition: transform 0.3s;
	position: relative;
	transform-origin: 0 0;
	&::before,
	&::after {
		content: "";
		position: absolute;
		background-color: var(--yellow-color);
		transform: translate(-50%, -50%);
		transition: all 0.3s;
	}
	&::before {
		width: 100%;
		height: toRem(1);
	}
	&::after {
		width: toRem(1);
		height: 100%;
	}
}

//  
.spollers__body {
	color: var(--grey-color-400);
	padding: toRem(0) toRem(0) toRem(24) toRem(0);
	background-color: var(--yellow-color-100);
	line-height: 1.5;
	@media (max-width: $mobileSmall) {
		font-size: toRem(12);
		padding: toRem(0) toRem(0) toRem(18) toRem(0);
	}
}
