.testimonials {
	background-color: var(--white-color-200);
	padding-top: toRem(110);
	@media (max-width: $tablet) {
		padding-top: toRem(40);
	}
	// .testimonials__container

	&__container {
		max-width: toRem(1131);
	}

	// .testimonials__content

	&__content {
		@media (min-width: $mobile) {
			@include adaptiveValue("max-width", 645, 420, 0, 1131, 767.98);
			margin-left: auto;
			margin-bottom: toRem(-100);
		}

		position: relative;
		z-index: 2;
		&.content {
			.content__title {
				line-height: 1.1;
				@include adaptiveValue("font-size", 49, 30, 0, $containerWidth, 767.98);
				@media (max-width: $mobile) {
					@include adaptiveValue("font-size", 40, 24, 0, 767.98, 320);
				}
				@media (max-width: $mobileSmall) {
					&.title-decor {
						&::after {
							width: 70%;
						}
					}
				}
			}
		}
	}

	// .testimonials__body

	&__body {
	}

	// .testimonials__slider

	&__slider {
		padding-top: toRem(22);
	}

	// .testimonials__wrapper

	&__wrapper {
	}
}

.testimonial-slide {
	background-color: var(--white-color-200);
	// .testimonial-slide__body

	&__body {
		display: grid;
		gap: toRem(25);
		@media (min-width: $mobile) {
			grid-template-columns: 38.5% 1fr;
		}
	}

	// .testimonial-slide__avatar

	&__avatar {
		align-self: end;

		img {
			object-position: bottom;
		}
		position: relative;
		&::before {
			content: "";
			position: absolute;
			top: toRem(-30);
			right: 30%;
			width: percent(100, 318);
			height: percent(100, 533);
			z-index: -1;
			background: url("@img/decors/decor-dots.png") 0 0 / contain no-repeat;
			@media (max-width: $mobile) {
				top: 0;
			}
		}
		@media (min-width: $mobileSmall) {
			padding-bottom: percent(574, 418);
		}
		@media (max-width: $mobileSmall) {
			width: toRem(160);
			height: toRem(220);
			justify-self: end;
			margin-top: -132px;
		}
		@media (max-width: $mobile) {
			order: 2;
			transform: scaleX(-1);
			// width: toRem(160);
			// height: toRem(240);
		}
	}

	// .testimonial-slide__info

	&__info {
		padding-bottom: toRem(50);
		@media (min-width: $mobile) {
			max-width: 35.5625rem;
			margin-top: toRem(110);
		}
	}

	// .testimonial-slide__quote

	&__quote {
		font-weight: 400;
		line-height: 1.7;
		letter-spacing: toRem(0.3);
		display: flex;
		flex-direction: column;
		gap: toRem(21);
		@include adaptiveValue("font-size", 20, 16, 0, $containerWidth, 767.98);
		&::before {
			content: "";
			width: toRem(64);
			height: toRem(64);
			background: url("@img/decors/quote.png") 0 0 / contain no-repeat;
			@media (max-width: $mobileSmall) {
				width: toRem(30);
				height: toRem(30);
			}
		}
		&:not(:last-child) {
			margin-bottom: toRem(20);
		}
		@media (max-width: $mobile) {
			order: 1;
			@include adaptiveValue("font-size", 22, 14, 0, 767.98, 320);
		}
	}

	// .testimonial-slide__name

	&__name {
		font-family: var(--spare-font);
		font-size: toRem(24);
		font-weight: 600;
		letter-spacing: toRem(0.3);
		display: inline-block;
		&:not(:last-child) {
			margin-bottom: toRem(20);
		}
	}
	&__pagination {
		margin-left: toRem(5);
	}
}
