.comparison {
	// .comparison__container

	&__container {
	}

	// .comparison__body

	&__body {
		@media (min-width: $mobile) {
			padding: toRem(45) toRem(30);
			border-radius: toRem(20);
			border: toRem(1) solid rgba(0, 0, 0, 0.2);
			box-shadow: 0 0 toRem(33.4) 0 rgba(0, 0, 0, 0.05);
		}
	}

	// .comparison__header

	&__header {
		text-align: center;
		&:not(:last-child) {
			margin-bottom: toRem(45);
			@media (max-width: $mobile) {
				margin-bottom: toRem(21);
			}
		}
		&.content {
			.content__title {
				display: inline-block;
				@media (max-width: $mobile) {
					text-align: left;
					&.title-decor {
						&::after {
							display: none;
						}
					}
				}
			}
		}
	}

	&__wrapper {
		max-width: toRem(908);
		margin: 0 auto;
		display: flex;
		flex-direction: column;
		@media (max-width: $mobile) {
			display: none;
		}
	}

	// mobile version

	&__mobile {
		@media (min-width: $mobile) {
			display: none;
		}
		@media (max-width: $mobile) {
			display: grid;
			gap: toRem(7);
			form {
				margin-top: toRem(12);
			}
		}
	}

	&__item {
		display: grid;
		grid-template-columns: 34% 1fr;
		gap: toRem(20);
		justify-content: start;
		padding: toRem(8) 0;
		align-items: start;
		&:not(:last-child) {
			border-bottom: toRem(1) solid rgba(0, 0, 0, 0.05);
		}
	}

	&__title {
		@media (max-width: $mobile) {
			font-family: var(--secondary-font);
			font-size: toRem(16);
			font-weight: 500;
			color: var(--white-color);
		}
	}

	&__label {
	}

	//========================================================================================================================================================

	&__top {
		display: grid;
		grid-template-columns: 1fr toRem(172) 1fr;
		gap: toRem(25);
		justify-items: center;

		&:not(:last-child) {
			@media (min-width: $mobile) {
				margin-bottom: toRem(20);
			}
		}

		@media (max-width: $mobile) {
			grid-template-columns: 34% 1fr;
			gap: toRem(20);
			justify-items: start;
			padding: toRem(10) 0;
			position: relative;
			&:not(:last-child) {
				margin-bottom: toRem(12);
			}
			&::before,
			&::after {
				content: "";
				position: absolute;
				width: 100vw;
				height: 100%;
				background-color: var(--yellow-color);
				z-index: -1;
			}
			&::before {
				left: 0;
			}
			&::after {
				right: 0;
			}
		}
	}

	&__label {
		font-family: var(--secondary-font);
		display: flex !important;
		flex-direction: column;
		font-weight: 500;
		gap: toRem(10);
		letter-spacing: toRem(0.3);
		line-height: 1.3;
		display: inline-block;
		text-align: center;
		@include adaptiveValue("font-size", 24, 20, 0, 908, 768);
		&:not(:last-child) {
			// margin-bottom: toRem(28);
		}
		&.title-decor {
			&::after {
				margin-top: auto;
				height: toRem(2);
			}
		}
		@media (max-width: $mobile) {
			font-size: toRem(14);
			font-weight: 600;
			text-align: left;
			font-family: var(--primary-font);
		}
	}

	&__items {
		display: grid;
		gap: toRem(50);

		&:not(:last-child) {
			margin-bottom: toRem(45);
		}
		@media (max-width: $tablet) {
			gap: toRem(30);
		}

		@media (max-width: $mobile) {
			gap: toRem(0);
		}
	}

	// .comparison__items

	&__column {
		display: grid;
		grid-template-columns: 1fr toRem(172) 1fr;
		gap: toRem(25);
		text-align: center;
		align-items: center;
	}

	// .comparison__item

	&__value {
		letter-spacing: toRem(0.3);
		line-height: 1.65;
		&_orange {
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: var(--yellow-color);
			font-weight: 600;
			color: var(--white-color);
			min-width: toRem(172);
			min-height: toRem(47);
			padding: toRem(10);
			clip-path: polygon(10% 0%, 90% 0%, 100% 50%, 90% 100%, 10% 100%, 0% 50%);
		}
		@media (max-width: $mobile) {
			font-size: toRem(14);
			line-height: normal;
		}
	}
	form {
		align-self: center;
	}
}
