body {
	// The scroll is blocked

	// The site is downloaded
	.loaded & {
	}
}
.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	@supports (overflow: clip) {
		overflow: clip;
	}
	// We press Footer
	> main {
		flex: 1 1 auto;
	}
	// Fix for sliders
	> * {
		min-width: 0;
	}
}

//========================================================================================================================================================
.page {
	// .page__hero

	&__hero {
	}
	&__about {
		padding-top: toRem(128);
		padding-bottom: toRem(128);
		@media (max-width: $mobile) {
			padding-top: toRem(50);
			padding-bottom: toRem(50);
		}
	}
	// .page__advantages

	&__advantages {
	}
	&__testimonials {
	}
	&__comparison {
		&:not(:last-child) {
			margin-bottom: toRem(80);
			@media (max-width: $mobile) {
				margin-bottom: toRem(30);
			}
		}
	}
}
//========================================================================================================================================================

.content {
	// .content__title

	&__title {
		font-family: var(--secondary-font);
		@include adaptiveValue("font-size", 44, 24);
		font-weight: 500;
		line-height: 1.3;
		letter-spacing: toRem(0.4);
		&:not(:last-child) {
			margin-bottom: toRem(20);
		}
		@media (max-width: $mobileSmall) {
			&:not(:last-child) {
				margin-bottom: toRem(10);
			}
		}
		&_big {
			@include adaptiveValue("font-size", 45, 23);
		}
	}

	// .content__text

	&__text {
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}

	// .content__button

	&__button {
	}
	&_center {
		text-align: center;
	}
	&_white {
		color: var(--white-color);
	}
	&_left {
		text-align: left;
	}
}

.text {
	line-height: 1.65;
	p {
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
	}
}

.title-decor {
	&::after {
		content: "";
		width: 100%;
		display: block;
		height: toRem(1);
		margin-top: toRem(12);
		background-color: rgba(255, 185, 121, 0.8);
	}
}

.ibg {
	position: relative;
	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
	}
	&_contain {
		img {
			object-fit: contain;
		}
	}
}

.sr-only {
	position: absolute;
	width: toRem(1);
	height: toRem(1);
	padding: 0;
	margin: toRem(-1);
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border-width: 0;
}
