.hero {
	position: relative;
	&::before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background-color: var(--primary-dark-color);
		z-index: 1;
	}
	// .hero__container

	&__container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		min-height: 100vh;
	}

	// .hero__content

	&__content {
		color: var(--white-color);
		padding: toRem(30) 0;
		max-width: toRem(920);
		position: relative;
		z-index: 2;
		margin: 0 auto;
		&.content {
			.content__title {
				&:not(:last-child) {
					margin-bottom: toRem(20);
					@media (max-width: $mobileSmall) {
						margin-bottom: toRem(6);
					}
				}
				&_big {
				}
			}

			.content__text {
				@include adaptiveValue("font-size", 20, 14);
				&:not(:last-child) {
					margin-bottom: toRem(20);
					@media (max-width: $mobileSmall) {
						margin-bottom: toRem(10);
					}
				}
			}
		}
	}

	// .hero__bg

	&__bg {
		position: absolute;
		width: 50vw;
		height: 100%;
		top: 0;
		object-fit: cover;
		// .hero__bg_1

		&_1 {
			left: 0;
		}
		&_2 {
			right: 0;
		}
	}
}
